import { Menu, Icon } from 'antd'
import GraduationIcon from '../static/graduation-hat.svg'
import BriefcaseIcon from '../static/briefcase.svg'


import './MyMenu.css'

const { SubMenu } = Menu


import { Anchor } from 'antd';

const { Link } = Anchor;


const handleClick = (e, link) => {
  e.preventDefault();
  console.log(link);
};

export default () => {
  const handleClick = e => { console.log('click ', e) }

  return (
    <div className='menu-border'>
      <Menu
          onClick={handleClick}
          // style={{ width: 256 }}
          // defaultSelectedKeys={['1']}
          defaultSelectedKeys={[]}
          defaultOpenKeys={['highlights']}
          mode="inline"
      >
          <Anchor affix={false} onClick={handleClick}>
          <SubMenu
              key="contact"
              title={
                <Link href="#avatar" title="API">
                  <><Icon type="mail" /><span>Contact</span></>
                </Link>
              }
          >
              <Menu.Item key="email">Email</Menu.Item>
              <Menu.Item key="linkedin">LinkedIn</Menu.Item>
              <Menu.Item key="github">GitHub</Menu.Item>
          </SubMenu>
          </Anchor>
          <SubMenu
              key="bio"
              title={<><Icon type="user" /><span>Bio</span></>}
          >
              <Menu.Item key="about">About</Menu.Item>
              <Menu.Item key="cv">CV</Menu.Item>
          </SubMenu>
          <SubMenu
              key="highlights"
              title={<><Icon type="trophy" /><span>Highlights</span></>}
          >
              <Menu.ItemGroup key="g1" title="Item 1">
                  <Menu.Item key="1">Option 1</Menu.Item>
                  <Menu.Item key="2">Option 2</Menu.Item>
              </Menu.ItemGroup>
              <Menu.ItemGroup key="g2" title="Item 2">
                  <Menu.Item key="3">Option 3</Menu.Item>
                  <Menu.Item key="4">Option 4</Menu.Item>
              </Menu.ItemGroup>
          </SubMenu>
          <SubMenu
            key="academics"
            title={<><GraduationIcon className='grad-icon' /><span>Academics</span></>}
          >
            <Menu.Item key="master">Master's Degree</Menu.Item>
            <Menu.Item key="undergrad">Bachelor's Degree</Menu.Item>
          </SubMenu>
          <SubMenu
            key="professional"
            title={<><BriefcaseIcon className='grad-icon' /><span>Professional</span></>}
          >
            <Menu.Item key="9">Option 9</Menu.Item>
            <Menu.Item key="10">Option 10</Menu.Item>
            <Menu.Item key="11">Option 11</Menu.Item>
            <Menu.Item key="12">Option 12</Menu.Item>
          </SubMenu>
          <SubMenu
            key="skills"
            title={<><Icon type='star' /><span>Skills</span></>}
          >
            <Menu.Item key="9">Option 9</Menu.Item>
            <Menu.Item key="10">Option 10</Menu.Item>
            <Menu.Item key="11">Option 11</Menu.Item>
            <Menu.Item key="12">Option 12</Menu.Item>
          </SubMenu>
        </Menu>
      </div>
  )
}